"use client";

import React, { useEffect, useState } from 'react';
import { useRouter } from 'next/navigation';
import { Bot, Search, Filter, MoreHorizontal, ArrowUpRight, Lock, Crown, Activity, AlertTriangle } from 'lucide-react';

type Tier = 'Growth' | 'Enterprise';

interface Deal {
  dealId: string;
  dealName: string;
  tier: Tier;
  phase: string;
  status: 'active' | 'paused' | 'awaiting_human';
  blocker: string | null;
  probability: number;
  health: 'healthy' | 'at_risk' | 'critical';
}

export default function DealsPage() {
  const router = useRouter();
  const [deals, setDeals] = useState<Deal[]>([]);
  const [isLoading, setIsLoading] = useState(true);

  useEffect(() => {
    // Load from existing persistence layer
    const storedData = localStorage.getItem('sales-os-v1');
    if (storedData) {
      try {
        const parsed = JSON.parse(storedData);
        if (parsed.states) {
          setDeals(Object.values(parsed.states));
        }
      } catch (e) {
        console.error("Failed to parse SalesOS data", e);
      }
    }
    setIsLoading(false);
  }, []);

  const handleRowClick = (dealId: string) => {
    // Update global active deal pointer by URL param
    router.push(`/dashboard/ai-sales-agent?deal=${dealId}`);
  };

  const getStatusBadge = (status: string, blocker: string | null) => {
    if (status === 'paused') {
      return (
        <span className="inline-flex items-center gap-1.5 rounded-full bg-amber-500/10 px-2 py-0.5 text-[10px] font-medium text-amber-500 border border-amber-500/20">
          <Activity size={10} /> Paused
        </span>
      );
    }
    if (blocker) {
      return (
        <span className="inline-flex items-center gap-1.5 rounded-full bg-rose-500/10 px-2 py-0.5 text-[10px] font-medium text-rose-500 border border-rose-500/20">
          <Lock size={10} /> Blocked
        </span>
      );
    }
    return (
      <span className="inline-flex items-center gap-1.5 rounded-full bg-emerald-500/10 px-2 py-0.5 text-[10px] font-medium text-emerald-500 border border-emerald-500/20">
        <Activity size={10} /> Active
      </span>
    );
  };

  return (
    <div className="flex h-screen w-full flex-col bg-zinc-950 text-zinc-100">
      {/* Header */}
      <header className="flex h-14 shrink-0 items-center justify-between border-b border-zinc-800 px-6">
        <div className="flex items-center gap-2">
          <div className="flex h-8 w-8 items-center justify-center rounded-lg bg-zinc-900 border border-zinc-800 text-zinc-400">
            <Bot size={16} />
          </div>
          <span className="font-semibold tracking-tight">All Deals</span>
          <span className="ml-2 rounded-full bg-zinc-900 px-2 py-0.5 text-[10px] font-medium text-zinc-500">
            {deals.length}
          </span>
        </div>
        <div className="flex items-center gap-3">
          <div className="relative">
            <Search size={14} className="absolute left-2.5 top-2 text-zinc-500" />
            <input 
              type="text" 
              placeholder="Search pipeline..." 
              className="h-8 w-64 rounded-md border border-zinc-800 bg-zinc-900/50 pl-8 pr-3 text-xs text-zinc-200 placeholder:text-zinc-600 focus:border-indigo-500 focus:outline-none"
            />
          </div>
          <button className="flex h-8 items-center gap-2 rounded-md border border-zinc-800 bg-zinc-900/50 px-3 text-xs font-medium text-zinc-400 hover:text-zinc-200">
            <Filter size={14} /> Filter
          </button>
        </div>
      </header>

      {/* Table Area */}
      <div className="flex-1 overflow-auto p-6">
        <div className="overflow-hidden rounded-lg border border-zinc-800 bg-zinc-900/20">
          <table className="w-full text-left text-sm">
            <thead className="bg-zinc-900/50 text-[10px] uppercase font-semibold text-zinc-500">
              <tr>
                <th className="px-4 py-3">Deal Name</th>
                <th className="px-4 py-3">Tier</th>
                <th className="px-4 py-3">Phase</th>
                <th className="px-4 py-3">Probability</th>
                <th className="px-4 py-3">Status</th>
                <th className="px-4 py-3 text-right">Actions</th>
              </tr>
            </thead>
            <tbody className="divide-y divide-zinc-800/50">
              {isLoading ? (
                <tr>
                  <td colSpan={6} className="px-4 py-8 text-center text-xs text-zinc-500">Loading pipeline data...</td>
                </tr>
              ) : deals.map((deal) => (
                <tr 
                  key={deal.dealId} 
                  onClick={() => handleRowClick(deal.dealId)}
                  className="group cursor-pointer transition-colors hover:bg-zinc-900/40"
                >
                  <td className="px-4 py-3">
                    <div className="font-medium text-zinc-200 group-hover:text-indigo-400 transition-colors">
                      {deal.dealName}
                    </div>
                  </td>
                  <td className="px-4 py-3">
                    <div className="flex items-center gap-1.5">
                      {deal.tier === 'Enterprise' && <Crown size={12} className="text-amber-500" />}
                      <span className={`text-xs ${deal.tier === 'Enterprise' ? 'text-amber-200/80' : 'text-zinc-400'}`}>
                        {deal.tier}
                      </span>
                    </div>
                  </td>
                  <td className="px-4 py-3">
                    <span className="rounded-sm bg-zinc-800/50 px-1.5 py-0.5 text-[11px] text-zinc-400">
                      {deal.phase}
                    </span>
                  </td>
                  <td className="px-4 py-3">
                    <div className="flex items-center gap-2">
                      <div className="h-1.5 w-16 rounded-full bg-zinc-800 overflow-hidden">
                        <div 
                          className={`h-full rounded-full ${deal.probability > 70 ? 'bg-emerald-500' : 'bg-indigo-500'}`} 
                          style={{ width: `${deal.probability}%` }}
                        />
                      </div>
                      <span className="text-xs font-mono text-zinc-500">{deal.probability}%</span>
                    </div>
                  </td>
                  <td className="px-4 py-3">
                    {getStatusBadge(deal.status, deal.blocker)}
                  </td>
                  <td className="px-4 py-3 text-right">
                    <button className="invisible group-hover:visible p-1 text-zinc-500 hover:text-zinc-300">
                      <ArrowUpRight size={14} />
                    </button>
                  </td>
                </tr>
              ))}
            </tbody>
          </table>
        </div>
      </div>
    </div>
  );
}